import { Message, MessageBox, Notification } from "element-ui";

const m = {
  confirm(msg: any) {
    return new Promise(function(resolve, reject) {
      MessageBox.confirm(msg, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          resolve();
        })
        .catch(() => {
          reject();
        });
    });
  }
};

export default m;
