const typeMap: any = {
  xls: "application/vnd.ms-excel;charset=utf-8",
  xlsx: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=utf-8"
};

// ArrayBuffer转str
function ab2str(arrayBuffer: any) {
  return new Promise(resolve => {
    let fr = new FileReader();
    fr.onload = e => resolve((e.target as any).result);
    fr.readAsText(new Blob([arrayBuffer]), "UTF-8");
  });
}

export async function downloadBuffer(arrayBuffer: any, fileName = "下载", type = "xls") {
  // 校验数据
  let resJSON = {};
  if (arrayBuffer.byteLength < 4096) {
    // 优化 这里取4096为阈值 后端返回空excel时byteLength为4096 大于的都视为excel文件不再进行JSON转化
    try {
      // 尝试将buffer转为JSON 这里进行错误捕获预防错误
      // @ts-ignore
      resJSON = JSON.parse(await ab2str(arrayBuffer).toString()); // 考虑兼容 这里没用使用TextDecoder.decode方法
    } catch (e) {
      console.log(e);
    }
    if ("message" in resJSON) {
      throw new Error((resJSON as any).message);
    }
  }
  // 开始下载
  let a = document.createElement("a");
  a.href = (window.URL || (window as any).webkitURL).createObjectURL(
    new Blob([arrayBuffer], {
      type: typeMap[type]
    })
  );
  a.download = fileName + "." + type;
  a.dispatchEvent(new MouseEvent("click"));
}
