







































































import { Vue, Component, Emit, Watch } from "vue-property-decorator";
import Utils from "@/utils/utils";
import api from "../../../api";
import cookieUtils from "@/utils/cookie";
import { downloadBuffer } from "@/utils/downloadBuffer";

@Component({})
export default class PersonList extends Vue {
  // 表格信息
  tableData: any = [];
  loading: any = false;
  // 分页信息
  pagination: any = {
    current: 1,
    size: 10,
    total: 0,
    query: {
      name: "",
      organizationId: ""
    }
  };
  // 复选框选中行数据
  multipleSelection: Array<any> = [];
  // 组织机构数据
  orgOptions: any = [];
  //筛选组织
  filterOrgId: any = "";
  // 通用适配prop
  typeDetailProps: any = {
    label: "name",
    value: "id",
    checkStrictly: true
  };

  created() {
    this.getList(); //获取表格
    this.getOrgInfo(); //获取组织机构，查询条件
  }

  // 切换到详情页面（添加和编辑）
  @Emit("switch")
  switchToDetails() {}

  @Watch("filterOrgId")
  WatchFilterOrg(newParam: any, oldParam: any) {
    this.pagination.query.organizationId = Utils.lastOfArr(this.filterOrgId);
  }

  // 获取组织机构
  getOrgInfo() {
    this.$http.get(api.getNewTypeTree).then((res: any) => {
      if (res.status) {
        if (res.data.status) {
          this.orgOptions = res.data.data;
          Utils.removeEmptyChildren(this.orgOptions);
        } else {
          this.$message.error(res.data.message);
        }
      }
    });
  }

  // 根据关键字，每页查询数量，当前页获得所有列表
  search() {
    this.pagination.current = 1;
    this.getList();
  }

  getList() {
    this.loading = true;
    this.$http
      .post(api.getPaging, this.pagination)
      .then((res: any) => {
        this.loading = false;
        if (res.status) {
          if (res.data.status) {
            this.tableData = res.data.data.records;
            this.pagination.total = res.data.data.total;
          } else {
            this.$message.error(res.data.message);
          }
        }
      })
      .catch(() => {
        this.loading = false;
      });
  }

  handleCurrentChange(val: number) {
    this.pagination.current = val;
    this.getList();
  }

  handleSizeChange(val: number) {
    this.pagination.size = val;
    this.getList();
  }

  // 删除设备信息
  deletePersons(ids: any) {
    let _this = this;
    if (ids.length !== 0) {
      _this
        .$confirm("确认删除?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
        .then(() => {
          this.$http.post(api.multipleDelete, ids).then((response: any) => {
            if (response.data.status) {
              _this.$message.success(response.data.message);
              _this.getList();
            } else {
              _this.$message.error(response.data.message);
            }
          });
        })
        .catch(() => {});
    } else {
      _this.$message.warning("请选择要删除的数据！");
    }
  }

  // 单个删除
  handleDelete(row: any) {
    let ids: any = [];
    ids.push(row.id);
    this.deletePersons(ids);
  }

  // 批量删除
  multipleDelete() {
    let ids: any = [];
    this.multipleSelection.forEach(item => {
      ids.push(item.id);
    });
    this.deletePersons(ids);
  }

  // 获得所有复选框选中数据
  getAllSelected(val: Array<any>) {
    this.multipleSelection = val;
  }

  downloadExcel() {
    // let a = document.createElement("a");
    // a.href = api.download;
    // a.click();
    this.$http
      .get(api.download, {
        responseType: "arraybuffer"
      })
      .then((response: any) => {
        // console.log(response.data);
        // let url = window.URL.createObjectURL(
        //   new Blob([response.data], {
        //     type: "application/vnd.ms-excel;charset=utf-8"
        //   })
        // );
        // console.log(url);
        // let a = document.createElement("a");
        // a.href = url;
        // a.download = "测试.xls";
        // a.click();
        try {
          downloadBuffer(response.data, "导入人员模板");
        } catch ({ message }) {
          this.$message.error(message);
        }
      })
      .catch(() => {});
  }

  // 上传文件前判断文件格式、大小等
  beforeUpload(file: any) {
    const extension = file.name.substring(file.name.lastIndexOf(".") + 1) === "xls";
    // const isLt2M = file.size / 1024 / 1024 < 10
    if (!extension) {
      this.$message({
        message: "上传文件只能是 xls 格式!",
        type: "warning"
      });
    }
    // if(!isLt2M) {
    //     this.$message({
    //         message: '上传文件大小不能超过 10MB!',
    //         type: 'warning'
    //     });
    // }
    // return extension || extension2 && isLt2M
    return extension;
  }

  uploadFileSuccess(data: any, file: any) {
    const chosenFile: any = file;
    if (data.status) {
      this.$message.success(data.message);
      this.getList();
    } else {
      this.$message.error(data.message);
      this.getList();
    }
  }

  uploadFileError() {
    this.$message.error("操作失败！");
  }
}
