





















































































































































import { Component, Emit, Prop, Vue, Watch } from "vue-property-decorator";
import Utils from "@/utils/utils";
import PersonDevice from "./PersonDevice.vue";
import api from "../../../api";
import { ElTree } from "element-ui/types/tree";
import { ElForm } from "element-ui/types/form";
import { ElUpload, ElUploadInternalFileDetail } from "element-ui/types/upload";
import { validateImage } from "@/utils/validate";

@Component({
  components: { PersonDevice }
})
export default class PersonDetails extends Vue {
  loading: any = false; //form加载
  @Prop({ type: String })
  listRowId!: any;
  // 用于父组件给组件传值
  customOptions: Array<any> = [];
  // 人员基本属性表单(没有包括自定义属性)
  personDetailsForm: any = {};
  // 人员基本属性表单规则(没有包括自定义属性)
  personDetailsFormRules: any = {
    mobile: [
      {
        required: true,
        message: "该选项为必填项",
        trigger: "change"
      }
    ],
    typeDetailId: [
      {
        required: true,
        message: "该选项为必填项",
        trigger: "change"
      }
    ],
    realName: [
      {
        required: true,
        message: "该选项为必填项",
        trigger: "change"
      }
    ],
    roleIds: [
      {
        required: true,
        message: "该选项为必填项",
        trigger: "blur"
      }
    ],
    organizationIds: [
      {
        required: true,
        message: "该选项为必填项",
        trigger: "blur"
      }
    ]
  };
  // 人员类型选项
  personTypeVisible: boolean = false;
  personTypeOptions: Array<any> = [];
  // 当前用户能看到的所有组织
  orgOptions: Array<any> = [];
  orgVisible: boolean = false;
  // 组织角色
  roleOptions: Array<any> = [];
  // 输入框是否可输入，用于查看情况
  disabled: boolean = false;
  // 隐藏确定按钮，用于查看情况
  visible: boolean = true;
  // 分页信息
  pagination: any = {
    // 查询关键字
    condition: "",
    // 表格信息
    tableData: [],
    // 用于查询和显示某页数据
    currentPage: 1,
    // 分页条数
    pageSize: 10,
    // 总条数
    total: 0
  };
  // 传感器弹窗
  sensorDialogVisible: any = false;
  // 通用适配prop
  typeDetailProps: any = {
    label: "name",
    value: "id",
    checkStrictly: true
  };
  // 组织机构prop
  organizationProps: any = {
    label: "name",
    value: "id",
    checkStrictly: true,
    multiple: true
  };
  hasCustomRole: boolean = qTouch.getBaseTypeByCode("person");

  created() {
    this.getRoleList();
    this.getPersonTypes();
    this.getOrgInfo();
  }

  @Watch("listRowId", { immediate: true })
  listRowIdChange() {
    this.listRowId && this.getPersonInfo();
  }

  // 获取人员信息
  getPersonInfo() {
    this.loading = true;
    this.$http.get(api.getPersonInfoById + "?personId=" + this.listRowId).then((response: any) => {
      if (response.data.status) {
        this.personDetailsForm = response.data.data;
        this.personDetailsForm.birthDay = this.personDetailsForm.birthDay.trim();
        if (this.personDetailsForm.extInfoVO) {
          this.customOptions = this.personDetailsForm.extInfoVO;
        }
      } else {
        this.$message.error(response.data.message);
      }
    });
    this.loading = false;
  }

  // 切换到设备列表页面
  @Emit("switch")
  switchToPersonList() {}

  handleEditPersonType() {
    this.personTypeVisible = true;
    this.getPersonTypes();
  }

  // 获取人员类型
  getPersonTypes() {
    this.$http.get(api.findTypeTree + "/person").then((response: any) => {
      if (response.data.status) {
        this.personTypeOptions = response.data.data;
        // this.$nextTick(() => {
        //   let typeDetailId = this.personDetailsForm.typeDetailId;
        //   typeDetailId && (this.$refs.personTree as ElTree).setCheckedKeys([typeDetailId]);
        // });
      } else {
        this.$message.error(response.data.message);
      }
    });
  }

  // 类型树只允许单选
  handleTypeChange(data: any, checked: boolean, node: boolean) {
    if (checked) {
      (this.$refs.personTree as any).setCheckedKeys([data.id]);
    }
  }

  savePersonType() {
    // let typeDetailIds = (this.$refs.personTree as ElTree).getCheckedKeys();
    // this.$set(this.personDetailsForm, "typeDetailId", typeDetailIds.length ? typeDetailIds[0] : "");
    (this.$refs.personDetailsForm as ElForm).validateField("typeDetailId", () => {});
    // this.personTypeVisible = false;
    if (this.personDetailsForm.typeDetailId) {
      this.getCustomOptions();
    }
  }

  // 获得自定义属性
  getCustomOptions() {
    this.$http.get(api.getTemplateByTypeDetaiId + "/" + this.personDetailsForm.typeDetailId).then((response: any) => {
      if (response.data.status) {
        this.customOptions = response.data.data;
      } else {
        this.$message.error(response.data.message);
      }
    });
  }

  // 编辑所属组织
  handleEditOrg() {
    this.orgVisible = true;
    this.getOrgInfo();
  }

  // 获取组织机构
  getOrgInfo() {
    this.$http.get(api.getNewTypeTree).then((res: any) => {
      if (res.data.status) {
        this.orgOptions = res.data.data;
        // this.$nextTick(() => {
        //   let organizationIds = this.personDetailsForm.organizationIds;
        //   organizationIds && (this.$refs.orgTree as ElTree).setCheckedKeys(organizationIds);
        // });
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  // 保存所选组织
  saveOrg() {
    // this.$set(this.personDetailsForm, "organizationIds", (this.$refs.orgTree as ElTree).getCheckedKeys());
    // this.orgVisible = false;
    (this.$refs.personDetailsForm as ElForm).validateField("organizationIds", () => {});
  }

  // 获取角色列表
  getRoleList() {
    this.$http.get(api.roleList).then((res: any) => {
      if (res.data.status) {
        this.roleOptions = res.data.data;
        for (let i in this.roleOptions) {
          if (this.roleOptions[i].defaultRole) {
            this.personDetailsForm.roleIds = [this.roleOptions[i].id];
            break;
          }
        }
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  // 自定义上传文件
  customHttpRequest(data: any) {
    let fData = Utils.createUploadForm(data.file);
    this.$http
      .post(data.action, fData.formData, fData.config)
      .then((response: any) => {
        if (response.data.status) {
          this.personDetailsForm.photo = response.data.data.accessUrl;
          this.doSave();
        } else {
          this.$message.error(response.data.message);
        }
      })
      .finally(() => {
        this.loading = false;
      });
  }

  // 获得组件CustomInput填充之后的数据
  async getFilledCustomOptions() {
    let customInput: any = this.$refs["custom-input"];
    if (customInput) {
      return await customInput.formValidation();
    }
    return null;
  }

  // 保存人员信息
  savePerson() {
    (this.$refs.personDetailsForm as any).validate(async (valid: any) => {
      if (valid) {
        let data = await this.getFilledCustomOptions();
        if (data) {
          this.personDetailsForm.extInfo = data;
        }
        // console.log(tempObject);
        //验证手机号是否绑定账号信息
        this.loading = true;
        this.$http
          .get(api.findByMobile, {
            params: {
              mobile: this.personDetailsForm.mobile
            }
          })
          .then((response: any) => {
            if (response.data.status) {
              if (response.data.data) {
                let res = response.data.data;
                if (res.id !== this.personDetailsForm.accountId) {
                  this.$confirm("手机号【" + res.mobile + "】已绑定用户【" + res.realName + "】，是否使用该用户名替换当前用户？", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    type: "warning"
                  }).then(() => {
                    this.personDetailsForm.accountId = res.id;
                    this.doSubmit();
                  });
                } else {
                  this.doSubmit();
                }
              } else {
                this.doSubmit();
              }
            } else {
              this.$message.error(response.data.message);
            }
          });
        this.loading = false;
      }
    });
  }

  doSubmit() {
    if (this.personDetailsForm.needUpload) {
      delete this.personDetailsForm.needUpload;
      (this.$refs.upload as ElUpload).submit();
    } else {
      this.doSave();
    }
  }

  // 提交后台保存
  doSave() {
    let url = api.add;
    if (this.listRowId) {
      url = api.update;
    }
    this.$http
      .post(url, this.personDetailsForm)
      .then((response: any) => {
        if (response.data.status) {
          this.$message.success(response.data.message);
          response.data.data && this.$emit("update:listRowId", response.data.data);
          this.switchToPersonList();
          let name = localStorage.getItem("userName");
          if (name === this.personDetailsForm.username) {
            localStorage.setItem("realName", this.personDetailsForm.realName);
            qTouch.emit("changeName", this.personDetailsForm.realName);
          }
        } else {
          this.$message.error(response.data.message);
        }
      })
      .finally(() => {
        this.loading = false;
      });
  }

  // 数据处理
  dataProcessing(data: Array<any>) {
    let tempArray: Array<any> = [];
    data.forEach(item => {
      tempArray.push(item.pop());
    });
    return tempArray;
  }

  // 上传
  customChange(file: ElUploadInternalFileDetail, fileList: ElUploadInternalFileDetail[]) {
    let { status, message } = validateImage(file.raw);
    if (status) {
      this.$set(this.personDetailsForm, "photo", file.url);
      this.personDetailsForm.needUpload = true;
    } else {
      this.$message.error(message);
      (this.$refs.upload as any).handleRemove(file);
    }
  }

  handleRemove() {
    this.personDetailsForm.photo = "";
    this.$nextTick(() => {
      (this.$refs.upload as ElUpload).clearFiles();
    });
  }
}
