





























































































import MessageBox from "@/utils/message";
import { Component, Emit, Prop, Vue } from "vue-property-decorator";
import api from "../../../api";

@Component({})
export default class PersonDevice extends Vue {
  @Prop({ type: String })
  personId!: any;

  deviceList: Array<any> = [];
  noBindDeviceList: Array<any> = [];
  noBindDeviceDialogVisible: boolean = false;

  selectedSensor: Array<any> = [];

  pageParam2: any = {
    size: 8,
    current: 1,
    total: 0,
    query: {
      name: ""
    }
  };
  addLoading = false;

  handleSizeChange2(val: any) {
    this.pageParam2.size = val;
    this.getNoBindList();
  }

  handleCurrentChange2(val: any) {
    this.pageParam2.current = val;
    this.getNoBindList();
  }

  handleSelectionChange(val: any) {
    this.selectedSensor = val;
  }

  //获取未绑定的设备列表
  getNoBindList() {
    this.pageParam2.query.bound = false;
    this.$http.post(api.getDevicePaging, this.pageParam2).then((res: any) => {
      if (res.data.status) {
        this.noBindDeviceList = res.data.data.records;
        this.pageParam2.total = res.data.data.total;
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  saveBind() {
    if (this.selectedSensor.length === 0) {
      this.$message.info("请选择要绑定的设备");
      return;
    }
    this.addLoading = true;
    let bindObjs = this.selectedSensor.map(value => {
      return { deviceId: value.id, gpsMark: false };
    });
    this.$http
      .post(api.bindPersonDevice + this.personId, bindObjs)
      .then((res: any) => {
        this.addLoading = false;
        if (res.data.status) {
          this.$message.success("绑定成功");
          this.noBindDeviceDialogVisible = false;
          this.selectedSensor = [];
          this.getList();
        } else {
          this.$message.error(res.data.message);
          this.noBindDeviceDialogVisible = false;
        }
      })
      .catch(() => {
        this.addLoading = false;
      });
  }

  getList() {
    this.$http.get(api.getPersonDevice + this.personId).then((res: any) => {
      if (res.data.status) {
        this.deviceList = res.data.data;
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  cancelBind(data: string) {
    MessageBox.confirm("确认需要取消绑定？")
      .then(() => {
        this.$http
          .post(api.cancelPersonDevice, {
            objId: this.personId,
            deviceId: data
          })
          .then((res: any) => {
            if (res.data.status) {
              this.$message.success("操作成功");
              this.getList();
            } else {
              this.$message.error(res.data.message);
            }
          });
      })
      .catch(() => {
        this.$message.info("已取消");
      });
  }

  bindDevice() {
    this.getNoBindList();
    this.selectedSensor = [];
    this.noBindDeviceDialogVisible = true;
  }

  changeBindGps(data: any) {
    this.$http
      .get(api.updateGps + data.personDeviceId, {
        params: {
          gpsMark: data.gpsMark
        }
      })
      .then((res: any) => {
        if (res.data.status) {
          this.getList();
          this.$message.success("操作成功");
        } else {
          this.getList();
          this.$message.error(res.data.message);
        }
      });
  }

  mounted() {
    this.getList();
  }
}
